.header {
  height: 8em;
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
}
.link1{
  padding: 0 !important;
  font-weight: 700;
    text-shadow: 0 4px 2px rgb(0 0 0 / 20%);
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    font-size: 1.2rem;
    justify-content: center;
    margin: 0.2em;
    position: relative;
    z-index: 1;
    border: 0.15em solid var(--clr-primary);
    color: var(--clr-primary);
    height: 2.2em;
    width: 2.2em;
}

.hovicon.effect-2.sub-b:hover, .hovicon.effect-2.sub-b:hover i {
  color: #fff;
}
.glow-on-hover {
 
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active {
  color: #000
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}




@media (max-width: 600px) {
  .header {
    height: 6em;
  }
}
